*****************
***** FROST *****
*****************

Frost is a lightweight CSS framework created by William J. Horn with the attempt of making layman-use of CSS much easier
and cleaner. It's main features are: 

    * Quick margin/padding classes
    * Easy interaction with flex box
    * Responsiveness with media query classes

This is an ongoing project and many more features will be added in the future.

