:root {
    --marginIncrement: 5px;
    --paddingIncrement: 5px;

    --fontSizeIncrement: 0.4rem;
}

.flex-row,
.flex-row-wrap,
.flex-column-wrap,
.flex-column {
    display: flex;
    flex-direction: row;
}

.flex-column-wrap,
.flex-column {
    flex-direction: column;
}

.flex-row-wrap,
.flex-wrap,
.flex-column-wrap {
    flex-wrap: wrap;
}

.flex-center {
    justify-content: center;
}

.flex-space-between {
    justify-content: space-between;
}

.flex-space-around {
    justify-content: space-around;
}

.flex-space-even {
    justify-content: space-evenly;
}

.flex-start {
    justify-content: flex-start;
}

.flex-end {
    justify-content: flex-end;
}

.flex-align-center {
    align-items: center;
}

.flex-align-start {
    align-items: flex-start;
}

.flex-align-end {
    align-items: flex-end;
}

/* todo: research more about flexbox and finish these later */
.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }
.flex-6 { flex: 6; }
.flex-7 { flex: 7; }
.flex-grow-01 { flex-grow: 0.1; }
.flex-grow-02 { flex-grow: 0.2; }
.flex-grow-03 { flex-grow: 0.3; }
.flex-grow-04 { flex-grow: 0.4; }
.flex-grow-05 { flex-grow: 0.5; }
.flex-grow-06 { flex-grow: 0.6; }
.flex-grow-07 { flex-grow: 0.7; }
.flex-grow-08 { flex-grow: 0.8; }

/* Columns, positioning, and centering */
.col-1 {
    width: calc(100%/12);
}

.col-2 {
    width: calc(100%/12*2);
}

.col-3 {
    width: calc(100%/12*3);
}

.col-4 {
    width: calc(100%/12*4);
}

.col-5 {
    width: calc(100%/12*5);
}

.col-6 {
    width: calc(100%/12*6);
}

.col-7 {
    width: calc(100%/12*7);
}

.col-8 {
    width: calc(100%/12*8);
}

.col-9 {
    width: calc(100%/12*9);
}

.col-10 {
    width: calc(100%/12*10);
}

.col-11 {
    width: calc(100%/12*11);
}

.col-12,
.fill-wide,
.fill {
    width: 100%;
}

.fill-tall,
.fill {
    height: 100%;
}

.center-wide {
    margin-left: auto;
    margin-right: auto;
}


